<template>
  <div class="mapLayers">
    <div class="title">图层管理</div>
    <el-tree
    :data="dataTree"
    show-checkbox
    node-key="id"
    ref="tree"
    :default-checked-keys="[5]"
    :default-expanded-keys="[1]"
    :props="defaultProps"
    @check="getCheckedNodes"
    >
    </el-tree>
  </div>
</template>

<script>
export default {
    name: 'mapLayers',
    data() {
        return {
            dataTree: [{
            id: 1,
            label: '单张图片服务图层',
            // disabled: true,
            children: [
                {
                    id : 5,
                    label: "夜景图片",
                    // disabled: true
                }
            ]
            },{
                id: 2,
                label: '在线天地图服务图层',
                children: [{
                    id : 6,
                    label: "天地图矢量图层"
                },
                {
                    id : 7,
                    label: "天地图矢量标注图层"
                },
                {
                    id : 8,
                    label: "天地图影像图层"
                },
                {
                    id : 9,
                    label: "天地图影像标注图层"
                }
            ]
            },{
                id: 3,
                label: 'ArcGis在线地图服务图层',
                children: [{
                    id : 10,
                    label: "ArcGis Online图层"
                },{
                    id : 11,
                    label: "ArcGis 深色图层"
                }]
            },
            {
                id: 4,
                label: '高德在线地图服务图层',
                children: [{
                    id : 12,
                    label: "高德在线矢量图层"
                },{
                    id : 13,
                    label: "高德在线影像图层"
                }]
            }
        ],
            
            // checkName: []  // 打开的图层 节点Label数组
        }
    },
    methods: {
        //获取图层数组名称
        getCheckedNodes(value,val) {
            
            // if (value.children) {
            //     console.log(value.children, 'value.children', val);
            // }
            // else {
                // if (val.checkedNodes.length === 0) {
                //     this.$emit('clearAllMapLayers', false)
                // }
                // else {
                    // console.log(val.checkedNodes);
                    const checkName = []
                    val.checkedNodes.forEach(element=> {
                        checkName.push(element.label)
                    })

                    const mapNames = checkName.filter((name)=> {
                        if (name.indexOf('服务') === -1) { //过滤掉图层组名称
                        return name
                        }
                    })

                   //底图 图层名称分类
                    let singleMapNames=[], arcGisNames=[], tdtNames=[], amapNames=[]
                    mapNames.forEach(name => {
                        if (name.indexOf('图片')> -1) {
                            singleMapNames.push(name)
                        }
                        else if (name.indexOf('天地图')> -1) {
                            tdtNames.push(name)
                        }
                        else if (name.indexOf('ArcGis')> -1) {
                            arcGisNames.push(name)
                        }
                        else if (name.indexOf('高德')> -1) {
                            amapNames.push(name)
                        }
                    });
                    const namesObject = {
                        singleMap: singleMapNames,
                        arcGis: arcGisNames,
                        tdt: tdtNames,
                        amap: amapNames
                    }
                    // console.log(namesObject, 'namesObject');
                    this.$emit('addMapLayers', namesObject)
                // }
                
            // }
           
        },
    }
}
</script>

<style scoped>
.mapLayers {
    width: 250px;
    height: 500px;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #2c3e50;
    opacity: 0.85;
    z-index: 999;
    margin: 10px;
}
.title {
    color: #fff;
    padding: 5px 0;
    margin-bottom: 10px;
    text-align: center;
    font-weight: 500;
    font-size: 22px;
    background: linear-gradient(to top, #66CCFF, #33daffb2, #009ccc);
}
.el-tree {
    background: none;
    color: #fff;
}
.el-tree /deep/ .el-tree-node__content:hover {
    background-color: #409eff !important
}

.el-tree /deep/ .el-tree-node__content {
    background-color: #2c3e50;
}

</style>