<template>
  <div id="cesiumContainer">
    <mapLayers  @addMapLayers="addMapLayers"/>
    <toolFunctions/>
    <div class="locationBar">
      {{`经度：${this.longitude}, 纬度：${this.latitude}, 视高: ${this.height}米 `}}
    </div>
    
  </div>
</template>

<script>
import mapLayers from "./LayerTree.vue"
import toolFunctions from "./ToolFunctions.vue"

let viewer
export default {
  name: "CesiumView",
  components: {
    mapLayers,
    toolFunctions
  },
  data() {
    return {
      // viewer: null,
      longitude: '',
      latitude: '',
      height: '',
    };
  },
  mounted() {
    this.initMap();
    // this.getposition()
    this.createEntity()
    // this.createLocationBar()
    
  },
  methods: {
    initMap() {
      var imageUrl = require("../cesium3D/images/singleMap.jpg");
      Cesium.Ion.defaultAccessToken =
            "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiIyNDBiMjBhOS1jZWE5LTRmYWYtOGI3Yy05ZDg3ODBiZDZiZjUiLCJpZCI6MjU5LCJpYXQiOjE2NTkzNjE0MTJ9.tXPPvw93XpCGHG71h7s_mMLlWGCuAL55o8LC309hLAg"
      return new Promise(()=>{
        viewer = new Cesium.Viewer("cesiumContainer",{
          baseLayerPicker: false,
          homeButton: false,
          imageryProvider: new Cesium.SingleTileImageryProvider({  //初始化加载地图时，加载单张图片
            rectangle: Cesium.Rectangle.fromDegrees(-180.0, -90.0, 180.0, 90.0),
            url: imageUrl
          })
        });
       
        

      })
    },

    // 测试功能--添加实体

    createEntity() {
      //定义几何体材质
      const material1 = new Cesium.Material({
        fabric: {
          // type:'Color',
          uniforms: {

          },
          source:`
            czm_material czm_getMaterial(czm_materialInput materialInput) {
              czm_material material = czm_getDefaultMaterial(materialInput);
              material.diffuse = vec3(materialInput,0.0);
              // material.alpha = 0.5;
              return material;
            }
          `
        }
      })


      console.log(material1);
      console.log(material1.shaderSource);
      //使用entity方式 添加几何实体
      const rectangle = viewer.entities.add({
        rectangle: {
          coordinates: Cesium.Rectangle.fromDegrees(90,20,110,30),
          // material: Cesium.Color.RED.withAlpha(0.5)
          material: material1
        }
      })


      //使用primitive 创建矩形
      const rectGeometry =  new Cesium.RectangleGeometry({
        rectangle: Cesium.Rectangle.fromDegrees(
          115,20,135,30
        ),
        height: 0,
        vertexFormat: Cesium.PerInstanceColorAppearance.VERTEX_FORMAT
      })

      const instance = new Cesium.GeometryInstance({
        geometry: rectGeometry,
        attributes: {
          color: Cesium.ColorGeometryInstanceAttribute.fromColor(
            Cesium.Color.RED.withAlpha(0.5)
          )
        }
      })

      const primitive = new Cesium.Primitive({
        geometryInstances: instance,
        // appearance: new Cesium.PerInstanceColorAppearance({
        //   flat: true
        // })
        appearance: new Cesium.MaterialAppearance({
            material : material1
          })
      })

      viewer.scene.primitives.add(primitive)
    },

    //实时获取鼠标位置信息
    getposition() {
      const that = this
      const handler = new Cesium.ScreenSpaceEventHandler(that.viewer.scene.canvas) 
      that.height = that.viewer.camera.positionCartographic.height.toFixed(2) //初始化获取相机高度并赋值
      that.viewer.camera.moveEnd.addEventListener(()=>{ //相机停止移动，监听相机高度事件
        that.height = that.viewer.camera.positionCartographic.height.toFixed(2) //相机高度
      })
      handler.setInputAction(function(movement){ //鼠标移动事件
        const cartesian = that.viewer.camera.pickEllipsoid(
          movement.endPosition,
          that.viewer.scene.globe.ellipsoid
        )
        if (cartesian || !that.height) {   
            const cartographic =  Cesium.Cartographic.fromCartesian(cartesian);
            that.longitude =   Cesium.Math.toDegrees(cartographic.longitude).toFixed(6);
            that.latitude =   Cesium.Math.toDegrees(cartographic.latitude).toFixed(6); 
            that.height = that.viewer.camera.positionCartographic.height.toFixed(2) //相机高度

            //俯仰角
            var pitch = Number(that.viewer.scene.camera.pitch).toFixed(2);
            //方向
            var heading =  Number(that.viewer.scene.camera.heading).toFixed(2);
        }
      },Cesium.ScreenSpaceEventType.MOUSE_MOVE)
    },
    //创建 底部位置信息 DIV
    createLocationBar() {
      const parDiv = document.getElementById('cesiumContainer').childNodes[1]
    
      const locationBar = document.createElement("div")
      console.log(parDiv, locationBar);
      locationBar.className = 'locationBar'
      locationBar.style = `
      position: absolute;
 
  width:100%;
  height:40px;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
  z-index: 100;`
      locationBar.innerHTML = `经度：${this.longitude}, 纬度：${this.latitude}, 视高: ${this.height}米 `
      parDiv.appendChild(locationBar)
      
    },
    //清除所有底图
    clearAllMap(val) {
      this.viewer.scene.globe.imageryLayers.removeAll()
      console.log(this.viewer.scene.globe.imageryLayers._layers);
    },
    //添加底图 图层
    addMapLayers(namesObject) {
      console.log(namesObject);
      if (namesObject.singleMap.length > 0) {
        const imageryLayer = new Cesium.ImageryLayer(new Cesium.SingleTileImageryProvider({  
            rectangle: Cesium.Rectangle.fromDegrees(-180.0, -90.0, 180.0, 90.0),
            url: require("../cesium3D/images/singleMap.jpg") 
        }))
        this.viewer.scene.globe.imageryLayers.add(imageryLayer)
      }
      else if(namesObject.singleMap.length === 0) {
        this.viewer.scene.globe.imageryLayers.removeAll()
      }
      if(namesObject.arcGis.length > 0 ) {
        namesObject.arcGis.forEach(element => {
          if (element.indexOf('Online')> -1) {
            const imageryLayer = new Cesium.ImageryLayer(new Cesium.ArcGisMapServerImageryProvider({ 
              url: `https://services.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer` 
            }))
            this.viewer.scene.globe.imageryLayers.add(imageryLayer)
          }
          if (element.indexOf('深色')> -1) {
            const imageryLayer = new Cesium.ImageryLayer(new Cesium.ArcGisMapServerImageryProvider({ 
              url: `http://map.geoq.cn/arcgis/rest/services/ChinaOnlineStreetPurplishBlue/MapServer` 
            }))
            this.viewer.scene.globe.imageryLayers.add(imageryLayer)
          }
        });      
      }
      else if(namesObject.arcGis.length === 0) {
        this.viewer.scene.globe.imageryLayers.removeAll()
      }
      // else if(namesObject.arcGisNames.length > 0) {
      //   const imageryLayer = new Cesium.ImageryLayer(new Cesium.ArcGisMapServerImageryProvider({ 
      //     url: `https://services.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer` 
      //   }))
      //   this.viewer.scene.globe.imageryLayers.add(imageryLayer)
      // }
      // else if(namesObject.tdtNames.length > 0) {

      // }
      // else if(namesObject.amapNames.length > 0) {

      // }







    },
  }
}
</script>

